import { CALENDAR_ACTIONS } from './Constants';

const initialState = {
  profileId: undefined,
  config: {},
  error: '',
  isLoading: false,
  property: undefined,
  appointments: [],
};

export const calendarReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case CALENDAR_ACTIONS.setConfig:
      return {
        ...state,
        config: payload,
      };
    case CALENDAR_ACTIONS.setStartOfWeek:
      return {
        ...state,
        startOfWeek: payload,
      };
    case CALENDAR_ACTIONS.setEndOfWeek:
      return {
        ...state,
        endOfWeek: payload,
      };
    case CALENDAR_ACTIONS.setError:
      return {
        ...state,
        error: payload,
      };
    case CALENDAR_ACTIONS.setIsLoading:
      return {
        ...state,
        isLoading: payload,
      };
    case CALENDAR_ACTIONS.setProfileId:
      return {
        ...state,
        profileId: payload,
      };
    case CALENDAR_ACTIONS.setProperty:
      return {
        ...state,
        property: payload,
      };
    case CALENDAR_ACTIONS.setAppointments:
      return {
        ...state,
        appointments: payload,
      };
    case CALENDAR_ACTIONS.addAppointments:
      return {
        ...state,
        appointments: [...state.appointments, ...payload],
      };
    default:
      return state;
  }
};
