import axios from 'axios';
import { Auth } from 'aws-amplify';
import { GetRequestConfig } from '../shared/Consts';
import _get from 'lodash/get';
import { PLACEHOLDER_AUTHORIZATION } from 'services/YouCanBook/constants';

axios.defaults.headers.get['Accept'] = 'application/json';
axios.interceptors.request.use(
  async config => {
    const customConfig = await getConfig(config);
    return customConfig;
  },
  function (error) {
    return Promise.reject(error);
  },
);
export default axios;

const getConfig = async config => {
  if (isYouBookMeRequest(config)) {
    return {
      ...config,
      headers: {
        Authorization: `Basic ${process.env.REACT_APP_YOU_BOOK_ME_AUTHORIZATION}`,
      },
    };
  }

  // Nile RESTful requests
  const { headers } = await GetRequestConfig(Auth);
  return {
    ...config,
    headers: {
      ...config.headers,
      ...headers,
    },
  };
};

const isYouBookMeRequest = config => {
  const authorization = _get(config, ['headers', 'Authorization']);
  return authorization === PLACEHOLDER_AUTHORIZATION;
};
