export const getOfferInfo = (offer: Offer): OfferInfo => {
  const funding = _get(offer, 'Funding.PurchaseType', '');

  return {
    funding,
    offerId: _get(offer, 'OfferId', ''),
    offerNo: _get(offer, 'OfferNo', ''),
    price: formatPrice(_get(offer, 'MappingCounter.OfferPrice', '')),
    financeContingency:
      funding === FUNDING_PURCHASE_TYPES.cash
        ? 'N/A'
        : _get(offer, 'MappingCounter.FinanceContingency', ''),
    inspectionContingency: _get(
      offer,
      'MappingCounter.InspectionContingency',
      '',
    ),
    saleOfHomeContingency: _get(
      offer,
      'MappingCounter.SaleOfHomeContingency',
      '',
    ),
    status: _get(offer, 'Step.messages.seller', ''),
  };
};

export const getSellerOffers = (data: any): Offer[] => {
  if (_isEmpty(data)) return [];
  const properties = _get(data, 'getUserOverview.Properties.Items', []);

  return properties.reduce((offers, property) => {
    const propertyOffers = _get(property, 'Offers');
    const propertyData = _get(property, 'PropertyData');
    const listPrice = _get(property, 'ListingDetails.ListPrice', 0);

    if (!_isEmpty(propertyOffers)) {
      const sellerOffers = propertyOffers.map(offer => ({
        ...offer,
        ListPrice: listPrice,
        PropertyData: propertyData,
      }));
      offers.push(...sellerOffers);
    }
    return offers;
  }, []);
};
